@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap');
/*font-family: 'EB Garamond', serif;*/

@font-face {
  font-family: helveticaNow;
  src: url("./font/HelveticaNowText-Light.otf");
}

@font-face {
  font-family: newMontreal;
  src: url("./font/NeueMontreal-Light.otf");
}

@font-face {
  font-family: newMontReg;
  src: url(./font/NeueMontreal-Regular.otf);
}

html, body{
  height: 100%;
  width: 100%;
}

body {
  margin: 0px;
  min-height: 100vh;
  min-width: 100vw;
  background-color: #0d0d0d;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;

  display: flex;

}

#root{
  display: flex;
  justify-content: space-between;
  margin: 20px 20px;
  padding: 0px 25px;
  border: .1px solid;

  width: 100%;
}


.aboutMe{
  font-family: newMontreal;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.aboutMe h1{
  margin-bottom: 0px;
  font-weight: 500;
}

.aboutMe h6{
  
  margin-top: 0px;
  font-weight: 100;
  
}

.navUl{
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 0px;
  gap: 10px;
}

.navUl a{
  text-decoration: none;
}

.navUl li{
  color: white;
  font-weight: 300;
  font-size: 15px;
  font-family: newMontReg;

}

.home, .skill, .contact, .skill2{
  display: flex;
  align-items: flex-end;
  font-family: newMontReg;
}

.home{
  width: 110px;
  text-align: left;
  animation: 1s ease-in changeOpacity ;
}

.projects{
  font-family:newMontReg;
  animation: 1s ease-in changeOpacity ;
}

.skill, .skill2{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: left;
  align-items: flex-start;
  animation: 1s ease-in changeOpacity;
}

.skill{
  transform: translateY(-10px);
  margin-bottom: 85px;
}

ul.contact{
  transform: scale(0.85);
}

ul.contact a{
  display: flex;
  align-items: center;
  gap: 10px;
}

ul.contact li:first-child{
  opacity: 0;
  animation: 2s cubic-bezier(0.4, 0, 0.2, 1)  changeOpacity infinite alternate;
  
}

ul.contact li:first-child+li{
  opacity: 0;
  animation: 2s cubic-bezier(0.4, 0, 0.2, 1) 0.4s changeOpacity infinite alternate;
  
}

ul.contact li:first-child+li+li{
  opacity: 0;
  animation: 2s cubic-bezier(0.4, 0, 0.2, 1)  1.2s changeOpacity infinite alternate;
  
}

ul.contact li:first-child+li+li+li{
  opacity: 0;
  animation: 2s cubic-bezier(0.4, 0, 0.2, 1)  0.8s changeOpacity infinite alternate; 
}

@keyframes changeOpacity {
  from {
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}



.projects{
  font-family:newMontReg;
  animation: 1s ease-in changeOpacity ;
}

ul.projects{
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  list-style-type: none;
  padding-left: 0px;

}

ul.projects li{

}

ul.projects a{
  color: white;
  transition: all 0.3s ease-in;
}

ul.projects a:hover{
  background-color: white;
  color: black;
}

.desc0, .desc1, .desc2, .desc3, .desc4{

  display: block;
  width:150px;
  font-size:14px;
  text-align: left;

  opacity: 0;
  transition: 1s ease opacity;

}

ul.skill, ul.skill2{
  list-style-type: none ;
}

.technical{
  max-width: 215px;
}

.lang li:hover, .framework li:hover, .tools li:hover, .technical li:hover {
  font-weight: 600;
}

.lightFont{
  font-family: newMontreal;
}

.listStyleType{
  list-style-type: none;
}


ul.contact a{
  text-decoration: none;
  color: white;
} 

